import { Accordion, AccordionSummary, Typography, AccordionDetails, Stack, List, ListItem } from "@mui/material";
import { styles } from "../styles";
import UnfoldMoreDoubleIcon from '@mui/icons-material/UnfoldMoreDouble';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';

export default function Expando({
    title,
    subtitle,
    body
}: {
    title: string;
    subtitle: string;
    body: string[];
}) {
    return (
        <Accordion>
            <AccordionSummary expandIcon={<UnfoldMoreDoubleIcon sx={{ color: 'white ' }} />} sx={styles.accordionHeader}>
                <Stack>
                    <Typography sx={styles.headerTitle}>{title}</Typography>
                    <Typography sx={styles.headerSubtitle}>{subtitle}</Typography>
                </Stack>
            </AccordionSummary>
            <AccordionDetails sx={styles.accordionBody}>
                <List>
                    {body.map((line, index) => (
                        <ListItem key={`ListItem-${title}-${index}`}>
                            <Stack direction={"row"} spacing={3}>
                                <ThumbUpOffAltIcon />
                                <Typography sx={{ color: `#a${index}a${index}ee` }}>
                                    {line}
                                </Typography>
                            </Stack>
                        </ListItem>
                    ))}
                </List>
            </AccordionDetails>
        </Accordion>);
}